.admin {
    .heading {
        font-size: 3rem;
        color: $primary;
        text-align: center;
        position: relative;

        i {
            position: absolute;
            left: 2rem;
            color: #000a;
            font-size: 1.5rem;
            top: 50%;
            transform: translateY(-50%) rotate(180deg);
            cursor: pointer;
        }
    }

    nav {
        margin-top: 2rem;
        border-bottom: 1px solid #0003;

        a {
            padding: 0 1.5rem;
            font-size: 1.5rem;
            font-weight: 600;
            text-align: center;

            &:hover {
                background: #e8e8e8;
                border-bottom: 3px solid $primary;
            }
        }

        .active-link {
            border-bottom: 3px solid $primary;
        }
    }

    section {
        height: 20vh;
        padding: 2rem 1rem 0 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    main {
        height: 80vh;
    }
}

.admin-user {
    display: flex;

    .therapist-input {
        transform: translateY(-.45rem);
        margin-top: 1rem;
        width: 18rem;
        padding: .4rem .5rem;
        font-size: 1.2rem;
        border-radius: 3px;
        box-shadow: 0 0 5pt 0.5pt #D3D3D322;
        border: 1px solid #eeea;
        transition: .5s all;

        &:focus {
            box-shadow: 0 0 5pt 0.5pt #D3D3D3;
        }

        margin-left: 2rem;
        margin-right: 2rem;
    }

    .center {
        justify-content: flex-start;
        align-items: center;
        margin-left: 3rem;
    }

    .add {
        margin-left: 1rem;
        cursor: pointer;

        &:hover {
            color: $patient;
        }
    }

    aside {
        flex: 1;
        height: 80vh;
        overflow: auto;
        padding: 2rem;

        h1 {
            text-align: center;
            font-size: 2.35rem;
        }
    }

    .list {
        .input-container {
            width: 65%;
            margin: 1rem auto;
        }

        .users {
            .user-card {

                width: 85%;
                margin: 1rem auto;
                display: flex;
                align-items: center;
                padding: .5rem 1rem;
                justify-content: space-between;
                border-radius: 4px;
                box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
                font-size: 1.25rem;
                font-weight: bold;

                .role {
                    color: $patient;

                    &.therapist {
                        color: $therapist;
                    }
                }
            }

            .left {
                display: flex;

                .name {
                    margin-right: .75rem;
                    border-right: 1px solid #000;
                    padding-right: .75rem;
                }
            }

            .delete {
                font-weight: normal;
                font-size: 1rem;
                cursor: pointer;
            }
        }
    }

    .create {
        border-left: 1px solid #0001;
        display: flex;
        flex-direction: column;

        .input-container {
            width: 80%;
            margin: .6rem auto;
        }

        .switch {
            margin: 1rem auto;

            button {
                padding: 0 .75rem;
                background-color: inherit;
                font-weight: bold;
                font-size: 1.2rem;

                &:last-child {
                    border-left: 1px solid #000;
                }

                &.patient {
                    color: $patient;
                }

                &.therapist {
                    color: $therapist;
                }
            }
        }

        .submit {
            background: $patient;
            color: #fff;
            padding: .5rem;
            border-radius: 4px;
            cursor: pointer;
            width: 8rem;
            margin: 0 auto;
            font-size: 1.2rem;
            font-weight: bold;
            margin-top: 1rem;

            &.therapist {
                background: $therapist;
            }
        }
    }
}

.admin-feedback {
    height: 80vh;
    padding: 2rem 0;
    overflow: auto;

    .input-container {
        width: 50%;
        margin: auto;
    }

    .feedback-card {
        width: 50%;
        margin: 1rem auto;
        background-color: #fff;
        box-shadow: 0 0 5pt 0.25pt #D3D3D344;
        padding: 1rem;
    }

    .top {
        display: flex;
        justify-content: space-between;
        font-size: 1.2rem;
    }

    .name {
        font-weight: bold;

        span {
            color: $patient;
        }
    }

    .faculty-name {
        font-weight: bold;

        span {
            color: $therapist;
        }
    }

    .message {
        font-size: 1.1rem;
        margin-top: .5rem;
        padding-top: .5rem;
        border-top: 1px solid #5555;
    }
}

.admin-actions {
    height: 80vh;
    padding: 2rem 0;
    overflow: auto;

    h2 {
        margin-bottom: .5rem;
    }

    .action {
        display: block;
        height: max-content;
        border-bottom: 1px solid #0005;
        padding-bottom: 1rem;

        .body {
            display: flex;
            align-items: flex-start;
        }
    }

    .file {
        display: flex;
        justify-content: space-between;
        margin-bottom: 2rem;
        margin-left: 1.5rem;

        label {
            margin-left: 1rem;
            cursor: pointer;
            background-color: inherit;
            border: 1px solid #ccc;
            color: $primary;
        }

        .name {
            width: 15rem;
            min-width: max-content;
            border-radius: 5px;
            border: 1px solid #eee;
            box-shadow: 0 0 5pt 0.25pt #D3D3D322;
            display: flex;
            align-items: center;
            padding-left: .5rem;
        }
    }

    .btn {
        margin-left: 1rem;
        background-color: $primary;

        &--alt {
            margin-left: 0;
        }

        &--2 {
            transform: translateY(.25rem);
        }
    }

    .input-container {
        margin-top: .25rem;
        margin-bottom: 1rem;
        margin-right: 1rem;
    }

    .inputs {
        display: flex;
    }
}